import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import "./Home.css";
import ReactPlayer from "react-player/youtube";
import { Doughnut } from "react-chartjs-2";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";

ChartJS.register(ArcElement, Tooltip, Legend);

const VideoPlayer = ({ video }) => {
  const [percentageWatched, setPercentageWatched] = useState(0);
  const [data, setData] = useState(null);

  useEffect(() => {
    const videoElement = document.getElementById("video");

    if (!videoElement) {
      return;
    }

    videoElement.addEventListener("timeupdate", handleTimeUpdate);

    return () => {
      videoElement.removeEventListener("timeupdate", handleTimeUpdate);
    };
  }, [video]);

  const handleTimeUpdate = (event) => {
    const video = event.target;
    setPercentageWatched((video.currentTime / video.duration) * 100);
    setData({
      datasets: [
        {
          data: [percentageWatched, 100 - percentageWatched],
          backgroundColor: ["black", "red"],
          borderWidth: 0,
        },
      ],
    });
  };
  return (
    <div className="video-player">
      {video ? (
        <>
          <ReactPlayer
            id="video"
            style={{
              marginTop: "25px",
            }}
            url={video.link}
            onTimeUpdate={handleTimeUpdate}
          />
          <div className="percentage-bar">
            <div className="percentage-bar-outer">
              <div
                className="percentage-bar-inner"
                style={{ transform: `rotate(${percentageWatched * 3.6}deg)` }}
              />
            </div>
          </div>
          {data ? (
            <Doughnut
              data={data}
              options={{
                cutoutPercentage: 85,
                legend: {
                  display: false,
                },
                tooltips: {
                  enabled: false,
                },
              }}
            />
          ) : (
            <p className="course__videos">Course Videos</p>
          )}
          <h1 className="video__title">{video.title}</h1>
          <hr className="hr__bar" />
          <p className="video__desc">{video.des}</p>
        </>
      ) : (
        <p>No video selected</p>
      )}
    </div>
  );
};
const VideoList = ({ videos, onVideoSelect }) => {
  return (
    <div className="videos">
      <div className="video__lists">
        <h5 className="course">Courses</h5>
        {videos.map((video) => (
          <div
            className="video__map"
            key={video.id}
            onClick={() => onVideoSelect(video)}
          >
            {video.title}
            <hr />
          </div>
        ))}
      </div>
    </div>
  );
};

const Home = () => {
  const [videos, setVideos] = useState([]);
  const [selectedVideo, setSelectedVideo] = useState(
    JSON.parse(localStorage.getItem("selectedVideo")) || null
  );
  useEffect(() => {
    localStorage.setItem("selectedVideo", JSON.stringify(selectedVideo));
  }, [selectedVideo]);
  useEffect(() => {
    const fetchVideos = async () => {
      const result = await axios.get("http://localhost:5000/video");
      setVideos(result.data.data);
    };

    fetchVideos();
  }, []);

  return (
    <div className="video__list">
      <VideoPlayer video={selectedVideo} />
      <VideoList videos={videos} onVideoSelect={setSelectedVideo} />
    </div>
  );
};

export default Home;
