import React from "react";
import styled from "styled-components";
import AccountCircleOutlinedIcon from "@mui/icons-material/AccountCircleOutlined";

const Container = styled.div`
  position: fixed;
  top: 0;
  background: orange;
  height: 56px;
  display: flex;
  justify-content: space-between;
`;

const Wrapper = styled.div`
  display: flex;
  align-items: center;

  height: 100%;
  padding: 0px 20px;
  position: relative;
`;

const User = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 500;
  color: white;
  margin-left: 75rem;
  width: 60px;
  height: 60px;
  cursor: pointer;
`;
const Item = styled.div`
  display: flex;
  align-items: center;
  gap: 20px;
  cursor: pointer;
  padding: 7.5px 0px;
  color: white;
  font-size: 25px;
  font-weight: 500;
`;

const Navbar = () => {
  return (
    <>
      <Container>
        <Wrapper>
          <Item>Kiwify</Item>
          <User>
            <AccountCircleOutlinedIcon
              style={{ width: "40px", height: "40px" }}
            />
          </User>
        </Wrapper>
      </Container>
    </>
  );
};

export default Navbar;
